import React from "react";
import {
    Wifi,
    Parking,
    Bar,
    AirConditioner,
    Reception24hs,
    Pool,
    Kids,
    Restaurant,
    Gym,
} from "@cvccorp-components/icons";
import { Paper, Divider } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
